.circle-loader {
  border: 0.125rem solid #e9ecef;
  border-left-color: #28a745;
  animation: loader-spin 1.2s infinite linear;
  position: relative;
  display: inline-block;
  vertical-align: top;
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
}

.load-complete {
  -webkit-animation: none;
  animation: none;
  border-color: #28a745;
  transition: border 500ms ease-out;
}

.checkmark {
  display: none;
}

.checkmark.draw::after {
  animation-duration: 800ms;
  animation-timing-function: ease;
  animation-name: checkmark;
  transform: scaleX(-1) rotate(135deg);
}

.checkmark::after {
  opacity: 1;
  height: 1rem;
  width: 0.5rem;
  transform-origin: left top;
  border-right: 0.125rem solid #28a745;
  border-top: 0.125rem solid #28a745;
  content: '';
  left: 0.25rem;
  top: 1rem;
  position: absolute;
}

@keyframes loader-spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes checkmark {
  0% {
    height: 0;
    width: 0;
    opacity: 1;
  }
  20% {
    height: 0;
    width: 0.5rem;
    opacity: 1;
  }
  40% {
    height: 1rem;
    width: 0.5rem;
    opacity: 1;
  }
  100% {
    height: 1rem;
    width: 0.5rem;
    opacity: 1;
  }
}

