.small-switch {
  position: relative;
  display: inline-block;
  width: 45px;
  height: 26px;
}

.small-switch .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  font-size: 0.75rem;
  color: #444654CC;
  background-color: #fff;
  padding-left: 3px;
  padding-top: 3px;
  border: solid 1px #444654CC;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.small-switch .slider.round {
  border-radius: 26px;
}

.small-switch .slider.round::before {
  border-radius: 50%;
}

.small-switch .slider::before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 3px;
  bottom: 3px;
  background-color: #444654CC;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.small-switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.small-switch input:checked + .slider {
  background-color: #eae3d4;
}

.small-switch input:checked + .slider::before {
  -webkit-transform: translateX(18px);
  -ms-transform: translateX(18px);
  transform: translateX(18px);
}

.range-slider {
  --range: calc(var(--max) - var(--min));
  --ratio: calc((var(--val) - var(--min)) / var(--range));
  --sx: calc(0.5 * 18 + var(--ratio) * (100% - 18));
  margin: 0;
  padding: 0;
  width: 100%;
  height: 3px;
  background: #eae3d4;
  outline: none;
}

.range-slider, .range-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
}

.range-slider::-webkit-slider-runnable-track {
  box-sizing: border-box;
  border: none;
  width: 100%;
  height: 3px;
  background: #eae3d4;
}

.js .range-slider::-webkit-slider-runnable-track {
  background: linear-gradient(#444654CC, #444654CC) 0/var(--sx) 100% no-repeat #eae3d4;
}

.range-slider::-moz-range-track, .range-slider::-ms-track {
  box-sizing: border-box;
  border: none;
  width: 100%;
  height: 3px;
  background: #eae3d4;
}

.range-slider::-moz-range-progress, .range-slider::-ms-fill-lower {
  height: 3px;
  background: #444654CC;
}

.range-slider::-webkit-slider-thumb {
  margin-top: -7.5px;
  width: 18px;
  height: 18px;
  background: #444654CC;
  cursor: pointer;
  border-radius: 50%;
  box-sizing: border-box;
  border: none;
}

.range-slider::-moz-range-thumb {
  width: 18px;
  height: 18px;
  background: #444654CC;
  cursor: pointer;
  border-radius: 50%;
  box-sizing: border-box;
  border: none;
}

.range-slider::-ms-thumb {
  margin-top: 0;
  width: 18px;
  height: 18px;
  background: #444654CC;
  cursor: pointer;
  border-radius: 50%;
  box-sizing: border-box;
  border: none;
}

.range-slider::-ms-tooltip {
  display: none;
}

